@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  min-width: 800px;
}

#titlePartOfNav {
  background-color: white;
  border-radius: 1em;
  color: black;
  padding: 0 1em;
  margin-left: 0.5em;
  width: 200px;
  text-align: center;
}

#restoreHideTickets {
    border: transparent;
    border-radius: 1em;
    cursor: pointer;
}
#restoreHideTickets:hover {
    cursor: pointer;
    background-color: #90cc75;
}
#restoreHideTickets:focus {
    outline: none;
}

* {
  font-family: "Raleway", sans-serif;
}
.ticket {
  margin: 2vw;
}
.headerTicket {
  display: flex;
}
.title {
  font-weight: bold;
  font-size: max(1.5vw, 22px);
  margin: 0.5vw 1vw;
}
.hideTicketButton {
  margin-left: auto;
}

.content {
  font-size: max(1vw, 14px);
  margin: 1vw;
  word-break: break-word;
}
.email,
.date {
  margin: 0 1vw;
  font-family: "Nunito", sans-serif;
  font-size: max(1vw, 12px);
}
.email {
  margin-right: 1em;
}
.footerTicket {
  display: flex;
}
.labels {
  margin-left: auto;
}
.label {
  background-color: rgb(211, 211, 211);
  padding: 0 1em;
  border-radius: 1em;
  margin: 0 0.5em;
}
.footerButton {
  margin-top: 1em;
  background-color: rgba(211, 211, 211, 0.5);

  display: flex;
  justify-content: center;
  visibility: hidden;
}
.MuiPaper-root:hover > .footerButton {
  visibility: visible;
}
.MuiPaper-root {
  transition: ease-in-out;
  transition: visability 1s;
}
.hiddenTicket {
  display: none;
}

.read-more-button {
  color: blue;
  cursor: pointer;
  margin-top: 0.5em;
}

